.App {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.step-header {
    margin-top: 0;
    margin-bottom: 30px;
}

.step-form:not(:last-child) {
    margin-bottom: 30px;
}

.step {
    min-width: 400px;
    padding: 30px;

    border-radius: 12px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.step-button-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.form-content-wrapper {
    margin-bottom: 30px;
}
